import React from "react";
import {Navbar as Nav, Button, Alignment} from "@blueprintjs/core";
import {Link} from "react-router-dom";
import {Auth} from "./Auth";

export const Navbar: React.FC = () => {
    return (
        <Nav>
            <Nav.Group align={Alignment.LEFT}>
                <Nav.Heading>My Site</Nav.Heading>
                <Nav.Divider/>
                <Link to='/'><Button className="bp4-minimal" icon="home" text="Home"/></Link>
                <Link to='/about'><Button className="bp4-minimal" icon="info-sign" text="About"/></Link>
                <Link to='/companies'><Button className="bp4-minimal" icon="grouped-bar-chart" text="Companies"/></Link>
                <Link to='/users'><Button className="bp4-minimal" icon="people" text="Users"/></Link>
            </Nav.Group>
            <Nav.Group align={Alignment.RIGHT}>
                <Auth/>
            </Nav.Group>
        </Nav>
    )
}

