import React, {useRef, useState} from "react";
import {Button, FormGroup} from "@blueprintjs/core";
import {Input} from "./Input";
import {getToken, registerUser} from "../Api/auth";
import {AxiosError} from "axios";
import {UserExistsResponse} from "../Types/auth";
import {Popup} from "./Popup";
import {useAppDispatch} from "../Hooks/hooks";
import {userActions} from "../Store/user";


const style = {
    maxWidth: '300px',
    margin: '0 auto',
}

export const RegisterForm = () => {
    const dispatch = useAppDispatch()
    const email = useRef<HTMLInputElement>(null)
    const password1 = useRef<HTMLInputElement>(null)
    const password2 = useRef<HTMLInputElement>(null)
    const firstName = useRef<HTMLInputElement>(null)
    const secondName = useRef<HTMLInputElement>(null)
    const [isLoading, setIsLoading] = useState<boolean>(false)
    const [message, setMessage] = useState<string | null>(null)
    const [errors, setErrors] = useState({
        email: false,
        password1: false,
        password2: false,
        firstName: false,
        lastName: false
    })
    const [popupOpen, setPopupOpen] = useState(false)
    const onSubmit = async () => {
        if (!validate()) {
            return
        }
        setIsLoading(true)
        try {
            const _password = password1.current!.value
            const _email = email.current!.value
            await registerUser({
                user_password: _password,
                user_password_repeat: _password,
                user_email: _email,
                user_firstname: firstName.current!.value,
                user_lastname: secondName.current!.value
            })
            const tokenResponse = await getToken({
                user_password: _password,
                user_email: _email
            })
            localStorage.setItem('token', tokenResponse.data.result.access_token)
            dispatch(userActions.login())
        } catch (e) {
            console.log(e)
            const exc = e as AxiosError<UserExistsResponse>
            setMessage(exc.response!.data.detail)
            setPopupOpen(true)

        } finally {
            setIsLoading(false)
        }
    }
    const validate = () => {
        let valid = true
        setErrors({
            email: false,
            password1: false,
            password2: false,
            firstName: false,
            lastName: false
        })
        const emailValid = email.current!.value && /^[\w-]+@([\w-]+\.)+[\w-]{2,4}$/.test(email.current!.value)
        if (!emailValid) {
            setErrors((prevState) => ({...prevState, email: true}))
            valid = false
        }
        const passwordValid = password1.current!.value && password1.current!.value.length >= 4 && password1.current!.value.length <= 20
        if (!passwordValid) {
            setErrors((prevState) => ({...prevState, password1: true}))
            valid = false
        }
        const passwordMatch = password1.current!.value === password2.current!.value
        if (!passwordMatch) {
            setErrors((prevState) => ({...prevState, password2: true}))
            valid = false
        }
        if (!firstName.current!.value) {
            setErrors((prevState) => ({...prevState, firstName: true}))
            valid = false
        }
        if (!secondName.current!.value) {
            setErrors((prevState) => ({...prevState, lastName: true}))
            valid = false
        }
        return valid
    }


    return (
        <>
            <FormGroup style={style}>
                <Input type='email' label='Email:' name='email' inputRef={email}
                       intent={errors.email ? 'danger' : undefined}/>
                <Input type='password' label='Password:' name='password1' inputRef={password1}
                       intent={errors.password1 ? 'danger' : undefined}/>
                <Input type='password' label='Repeat password:' name='password2' inputRef={password2}
                       intent={errors.password2 ? 'danger' : undefined}/>
                <Input type='text' label='First name:' name='firstName' inputRef={firstName}
                       intent={errors.firstName ? 'danger' : undefined}/>
                <Input type='text' label='Second name:' name='secondName' inputRef={secondName}
                       intent={errors.lastName ? 'danger' : undefined}/>
                <Button loading={isLoading} type='submit' onClick={onSubmit}>Submit</Button>
            </FormGroup>
            <Popup title='Warning!' message={message} open={popupOpen} setOpen={setPopupOpen}/>
        </>
    )
}

