import {User} from "../Types/auth";
import {loginUser} from "../Api/auth";
import {AppDispatch} from "./store";

const defaultState = {user: null}

type UserState = {
    user: User | null;
}


type SaveUser = {
    type: 'user/SaveUser';
    payload: User;
}


type Action = SaveUser

export const userReducer = (state: UserState = defaultState, action: Action): UserState => {
    switch (action.type) {
        case 'user/SaveUser':
            return {...state, user: action.payload};
        default:
            return state;
    }
}

const login = () => {
    return async (dispatch: AppDispatch) => {
        try {
            const loginResponse = await loginUser()
            const user = loginResponse.data.result
            const username = user.user_firstname + ' ' + user.user_lastname
            const email = user.user_email
            dispatch(saveUser({username: username, email: email}))
        } catch (e) {
            console.log(localStorage.getItem('token'))
        }
    }
}


const saveUser = (user: User): SaveUser => ({type: 'user/SaveUser', payload: user})

export const userActions = {saveUser, login}
