import React from "react";
import {Button, Dialog, DialogBody} from "@blueprintjs/core";

type PopupProps = {
    title: string;
    message: string | null;
    open: boolean;
    setOpen: CallableFunction;
}


export const Popup: React.FC<PopupProps> = ({title, message, open, setOpen}) => {
    const handleClose = () => setOpen(false);
    return (
        <div>
            <Dialog title={title} isOpen={open} canEscapeKeyClose={true} onClose={handleClose}>
                <DialogBody>
                    {message}<br/>
                    <Button onClick={handleClose}>Close modal</Button>
                </DialogBody>
            </Dialog>
        </div>
    );
}

