import {apiClient} from "./axios";
import {TokenRequest, TokenResponse, UserLoginResponse, UserRegisterRequest, UserRegisterResponse} from "../Types/auth";


export const registerUser = (user: UserRegisterRequest) => apiClient().post<UserRegisterResponse>('/user', user)

export const getToken = (user: TokenRequest) => apiClient().post<TokenResponse>('/auth/login', user)

export const loginUser = () => apiClient().get<UserLoginResponse>('/auth/me')


