import React, {useEffect, useState} from "react";
import {ModalWindow} from "../Components/ModalWindow";
import {useAppSelector} from "../Hooks/hooks";
import {getBackendHealth} from "../Api/axios";


export const MainPage: React.FC = () => {
    const [health, setHealth] = useState<{ info: string }>({info: 'Loading...'})
    useEffect(() => {
        async function func() {
            const data = await getBackendHealth()
            setHealth({info: data.data.result})
        }

        func()
    }, [])

    const {user} = useAppSelector(state => state.user)
    return (
        <>
            <p>Main Page</p>
            <p> {user?.username} </p>
            <p> {user?.email} </p>
            <ModalWindow message="My message"/>
            <p>Backend Status: {health.info.toUpperCase()}</p>
        </>)
};
