const defaultState = {size: 0}

type Add = {
    type: 'test/Add';
    payload: number;
}

type Subtract = {
    type: 'test/Subtract';
    payload: number;
}

type Action = Add | Subtract


export const test_reducer = (state = defaultState, action: Action) => {
    switch (action.type) {
        case 'test/Add':
            return {...state, size: state.size + action.payload}
        case 'test/Subtract':
            return {...state, size: state.size - action.payload}
        default:
            return state;
    }
}

const add = (number: number): Add => {return {type: 'test/Add', payload: number}}
const subtract = (number: number): Subtract => {return {type: 'test/Subtract', payload: number}}

export const actions = {add, subtract}
